<template>
    <div>
        <div style="margin-bottom:10px">
            <search-form :onAdd="onAdd" :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" @row-click="seeFunction"
                  @sort-change="sortChange" border
                  size="mini" v-loading="loading">
            <el-table-column align="center" label="状态" show-overflow-tooltip prop="casePartyList" sortable="custom" width="110">
                <template slot-scope="scope">
                    <span class="danger">{{scope.row.caseStatus}}</span>
                </template>
            </el-table-column>
            <el-table-column align="center" label="部门" prop="handlingDepartmentName" show-overflow-tooltip
                             sortable="custom"/>
            <el-table-column align="center" label="案发时间" show-overflow-tooltip prop="crimeTime" sortable="custom" width="110">
                <template slot-scope="scope">
                    {{scope.row.crimeTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="违法行为" prop="reasonName" show-overflow-tooltip sortable="custom"/>
            <el-table-column align="center" label="决定书编号" prop="pmDecisionCode" show-overflow-tooltip sortable="custom"/>
            <el-table-column align="center" label="当事人" prop="party" show-overflow-tooltip sortable="custom"/>
            <el-table-column align="center" label="处罚金额" show-overflow-tooltip prop="penaltyAmount" sortable="custom" width="100"/>
            <el-table-column align="center" label="实缴金额" show-overflow-tooltip prop="paidAmount" sortable="custom" width="100"/>
            <el-table-column align="center" label="缴款时间" show-overflow-tooltip prop="performanceTime" sortable="custom" width="110">
                <template slot-scope="scope">
                    {{scope.row.performanceTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="100">
                <template slot-scope="scope">
                    <el-tooltip :open-delay="500" content="查看" placement="top">
                        <el-link @click.stop="seeFunction(scope.row)" icon="el-icon-search" type="success"></el-link>
                    </el-tooltip>
                    <el-tooltip :open-delay="500" content="缴款" placement="top"
                                v-if="scope.row.paymentMethod == 2 &&( scope.row.performanceTime < 10 || !scope.row.paymentReceiptNumber)">
                        <el-link @click.stop="showAmount(scope.row)" icon="el-icon-coin" style="margin-left: 5px"
                                 type="warning"></el-link>
                    </el-tooltip>
                    <el-tooltip
                            :open-delay="500"
                            content="提交"
                            placement="top"
                            v-if="  ( scope.row.isProcessingPunish == 0|| scope.row.performanceTime > 10 )&& scope.row.caseStatus.indexOf('未')>0 && scope.row.caseStatus.indexOf('已')==0">
                        <el-link @click.stop="submitCase(scope.row)" icon="el-icon-upload2" style="margin-left: 5px"
                                 type="warning"></el-link>
                    </el-tooltip>

                    <el-tooltip :open-delay="500" content="修改" placement="top"
                                v-if="scope.row.caseStatus.indexOf('未')>0 ">
                        <el-link @click.stop="editFunction(scope.row)" icon="el-icon-edit" style="margin-left: 5px"
                                 type="primary" v-if="buttonCheck('edit')"></el-link>
                    </el-tooltip>
                    <el-tooltip :open-delay="500" content="删除当前项" placement="top">
                        <el-link @click.stop="deleteFunction( scope.row)" icon="el-icon-delete" style="margin-left: 5px"
                                 type="danger" v-if="buttonCheck('delete')"></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <edit-block :show.sync="editObj.show" :title="editObj.title" :type.sync="editObj.editId" @upDateList="getList"
                    v-if="editObj.show"/>
        <add-block :show.sync="addObj.show" :title="addObj.title" @upDateList="getList" v-if="addObj.show"/>
        <see-block :id="seeObj.id" :show.sync="seeObj.show"/>
        <i-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total" @getData="getList"
                      style="margin-top:10px"/>

        <el-dialog :visible="amount.show" @close="amount.show = false" center title="缴款" top="10vh" width="800px">
            <el-form :model="amount" :rules="amount.rules" label-width="130px" ref="ruleForm" status-icon>
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="缴款时间：" prop="performanceTime">
                            <el-date-picker
                                    placeholder="选择日期时间"
                                    type="date"
                                    v-model="amount.performanceTime"
                                    value-format="timestamp">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="缴款银行：" prop="payingBank">
                            <el-radio-group v-model="amount.payingBank">
                                <el-radio label="1">建设银行</el-radio>
                                <el-radio label="2">工商银行</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="银行单据编号：" prop="paymentReceiptNumber">
                            <el-input v-model="amount.paymentReceiptNumber"/>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="罚款金额：" prop="penaltyAmount">
                            <el-input disabled v-model="amount.penaltyAmount"/>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="实缴金额：" prop="paidAmount">
                            <el-input v-model="amount.paidAmount"/>
                        </el-form-item>
                    </el-col>
                </el-row>

            </el-form>
            <span class="dialog-footer" slot="footer">
                <el-button @click="amount.show = false" size="small" type="info">
                    <i class="iconfont icon-back"></i>取 消
                </el-button>
                <el-button @click="submitAmount('ruleForm')" size="small" type="primary">
                    <i class="iconfont icon-save"></i>保 存
                </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import iPagination from '../../../components/LjPagination'
    import searchForm from '../../../components/LjSearchForm'
    import editBlock from './component/editadd'
    import addBlock from './component/add'
    import {formatDate} from "../../../until/wordBook";
    import {
        apiAddPaidAmount,
        apiLefLisGett,
        apiSimpleCaseDelete,
        apiSimpleCaseSearch,
        submitSimpleCaseAPI
    } from "../../../api/case";
    import {apiOrgSearch} from "../../../api/system";
    import seeBlock from './component/formItem/see'

    export default {
        name: "easyCasePage",
        components: {searchForm, iPagination, editBlock, addBlock, seeBlock},
        data() {
            return {
                amount: {
                    show: false,
                    performanceTime: '',
                    paidAmount: '',
                    payingBank: '',
                    paymentReceiptNumber: '',
                    rules: {
                        performanceTime: [{required: true, message: '请选择缴款时间', trigger: 'change'}],
                        paymentReceiptNumber: [{required: true, message: '请填写单据编号', trigger: 'change'}],
                        paidAmount: [{required: true, message: '请填写实缴金额', trigger: 'change'}],
                        payingBank: [{required: true, message: '请选择缴款银行', trigger: 'change'}],
                    },
                    scId: ''
                },
                searchObj: [
                    {type: 'select', name: 'HandlingDepartment', options: [], label: '办案部门'},
                    {type: 'select', name: 'enforceId', options: [], label: '执法领域'},
                    {type: 'select', name: 'caseStatus', options: [], label: '案件状态'},
                    {
                        type: 'date',
                        dateType: 'daterange',
                        name: 'timeRange',
                        name1: 'startTime',
                        name2: 'endTime',
                        options: [],
                        label: '案发时间',
                        span: 12
                    },
                ],
                editObj: {
                    show: false,
                    title: "",
                    editId: ""
                },
                seeObj: {
                    show: false,
                    id: ''
                },
                addObj: {
                    show: false,
                    title: "新增简易案件登记",
                },
                total: 0,
                list: [],
                loading: true,
                form: {
                    pageNum: 1,
                    pageSize: 20
                }
            }
        },
        methods: {
            onAdd() {
                this.addObj.show = true;
            },
            submitCase(data) {
                this.$confirm('提交后将无法修改案件信息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    submitSimpleCaseAPI({scId: data.scId}).then(res => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '提交成功!'
                        });
                    })
                })
            },
            submitAmount() {
                this.$refs.ruleForm.validate(valid => {
                    if (valid) {
                        if (this.amount.paidAmount == this.amount.penaltyAmount) {
                            apiAddPaidAmount(this.amount).then(res => {
                                this.getList();
                                this.amount.show = false;
                                this.$message({
                                    type: 'success',
                                    message: '提交成功!'
                                });
                            })
                        } else {
                            this.$confirm('实缴金额与处罚金额不一致, 是否继续?', '提示', {
                                confirmButtonText: '确定',
                                cancelButtonText: '取消',
                                type: 'warning'
                            }).then(() => {
                                apiAddPaidAmount(this.amount).then(res => {
                                    this.getList();
                                    this.amount.show = false;
                                    this.$message({
                                        type: 'success',
                                        message: '提交成功!'
                                    });
                                })
                            })
                        }
                    }
                })
            },
            showAmount(data) {
                this.amount.scId = data.scId;
                this.amount.payingBank = data.payingBank;
                this.amount.penaltyAmount = data.penaltyAmount;
                this.amount.paidAmount = '';
                this.amount.paymentReceiptNumber = '';
                this.amount.performanceTime = '';
                this.amount.show = true
            },
            seeFunction(data) {
                this.seeObj.id = data.scId;
                this.seeObj.show = true
            },
            sortChange(data) {
                console.log(data)
                this.form.orderName = data.prop;
                this.form.sortName = data.order;
                this.getList();
            },
            deleteFunction(data) {
                this.$confirm('此操作将永久删除该案件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiSimpleCaseDelete(data.scId).then(res => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    })
                })
            },
            editFunction(row) {
                this.editObj.editId = row.scId;
                this.editObj.title = '修改简易案件';
                this.editObj.show = true;

            },
            getList() {
                this.loading = true;
                apiSimpleCaseSearch(this.form).then(res => {
                    console.log(res.data)
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false
                })
            },
            searchList(data) {  //data就是接收的this.form数据
                data.pageSize = this.form.pageSize;
                data.orderName = this.form.orderName;
                data.sortName = this.form.sortName;
                this.loading = true;
                console.log(data)
                apiSimpleCaseSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                })
            },
            initPage() {
                apiOrgSearch().then(res => {
                    res.data.forEach(i => {
                        i.value = i.orgId;
                        i.label = i.orgName;
                    });
                    this.searchObj.find(i => {
                        return i.name == 'HandlingDepartment'
                    }).options = res.data;
                    this.$until.workBookApi('caseStatus').then(res => {
                        res.forEach(i => {
                            i.value = i.dictValue;
                            i.label = i.dictName;
                        });
                        this.searchObj.find(i => {
                            return i.name == 'caseStatus'
                        }).options = res;
                        apiLefLisGett({pageSize: 1000, pageNum: 1}).then(res => {
                            res.data.forEach(i => {
                                i.value = i.enforceId;
                                i.label = i.enforceName;
                            });
                            this.searchObj.find(i => {
                                return i.name == 'enforceId'
                            }).options = res.data;
                        })
                    });
                });
                this.form.pageNum = 1;
                this.getList()
            }
        },
        created() {
            this.initPage()
        },
        filters: {
            formatDate(value) {
                return formatDate(value, 'yy/MM/dd hh:mm')
            },
        },
    }
</script>

<style scoped>

</style>
