<template>
    <div>
        <el-dialog :title="title" :visible="show" @close="closeDialog" center top="5vh" width="80%" class="easyCase">
            <s-scroll v-if="!loading">
                <s-scroll-item title="当事人信息">
                    <party-form :form="form" ref="step1" :case-part-list="casePartList"/>
                </s-scroll-item>
                <s-scroll-item title="案件信息">
                    <case-form :form="form" ref="step2"/>
                </s-scroll-item>
                <s-scroll-item title="违法信息">
                    <illegal-form :amountList.sync="amountList" :form="form" ref="step3"/>
                </s-scroll-item>
                <s-scroll-item title="处罚信息">
                    <step2
                            :amountList="amountList"
                            :form2="form"
                            :quotaInvoiceDe="quotaInvoice?quotaInvoice:false"
                            :type.sync="type"
                            ref="step4"
                    />
                </s-scroll-item>
                <s-scroll-item title="办案信息">
                    <work-form :form="form" ref="step5"/>
                </s-scroll-item>
                <s-scroll-item title="文书上传">
                    <book-form
                            :fileList="fileList"
                            :form2="form"
                            :type.sync="type"
                            @getFileList="getFileList"
                            ref="step6"/>
                    <el-form class="text-css" label-width="140px" size="small">
                        <el-form-item label="案件标签：">
                            <el-tag
                                    :disable-transitions="false"
                                    :key="tag.labId"
                                    @close="handleClose(tag)"
                                    closable
                                    v-for="tag in dynamicTags"
                            >{{tag.labelName}}
                            </el-tag>
                            <el-select
                                    @change="handleInputConfirm"
                                    allow-create
                                    class="input-new-tag"
                                    default-first-option
                                    filterable
                                    placeholder="请选择文章标签"
                                    ref="saveTagInput"
                                    size="small"
                                    v-if="inputVisible"
                                    v-model="inputValue"
                            >
                                <el-option
                                        :key="item.labId"
                                        :label="item.labelName"
                                        :value="item.labelName"
                                        v-for="item in labelList"
                                ></el-option>
                            </el-select>
                            <el-button @click="showInput" class="button-new-tag" size="small" v-else>+ 添加标签</el-button>
                        </el-form-item>
                        <el-form-item label="备 注：">
                            <el-input v-model="form.remarks"></el-input>
                        </el-form-item>
                    </el-form>
                </s-scroll-item>
            </s-scroll>

            <div class="checkIdCard">
                <intelligent-assistant :prop="form" ref="intelligent"/>
            </div>
            <span class="dialog-footer" slot="footer">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>取 消
        </el-button>
        <el-button @click="submitForm('ruleForm')" size="small" type="primary">
          <i class="iconfont icon-save"></i>保 存
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {
        apiFileGetDocList,
        apiSearchLabel,
        apiSimpleCaseAdd,
        apiSimpleCaseAddCaseLabel,
        apiSimpleCaseDeleteCaseLabel,
        apiSimpleCaseDetailGet,
        apiSimpleCaseGetLabel,
        apiSimpleCaseUpdate
    } from "../../../../api/case";
    import partyForm from "./formItem/party";
    import bookForm from "./formItem/book";
    import caseForm from "./formItem/caseTimeAddress";
    import illegalForm from "./formItem/illegalMsg";
    import workForm from "./formItem/workMsg";
    import step2 from "./formItem/step2";
    import sScroll from "../../../../components/LjScroll";
    import sScrollItem from "../../../../components/LjScrollItem";
    import intelligentAssistant from '../component/intelligentAssistant'

    export default {
        name: "edit",
        components: {
            step2,
            partyForm,
            caseForm,
            workForm,
            illegalForm,
            sScrollItem,
            intelligentAssistant,
            sScroll,
            bookForm
        },
        props: {
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "编辑"
            },
            type: {
                type: String
            }
        },
        computed: {
            filterList() {
                return function (o) {
                    return this.fileList.filter(i => {
                        return i.dtId == o;
                    });
                };
            }
        },
        data() {
            return {
                amountList: [],
                stepNum: "",
                labelList: [],
                loading: true,
                dynamicTags: [],
                fileList: [],
                documentList: [],
                cityOption: [],
                districtOption: [],
                casePartyList:[],
                streetOfficeOption: [],
                form: {
                    crimeTime: "",
                    province: "",
                    city: "",
                    district: "",
                    paidAmount:"",
                    illegalTypes: "",
                    streetOffice: "",
                    latitude: "",
                    longitude: "",
                    crimeLocal: "",
                    caseRegSource: "pc",
                    casePartyList:[],
                    legalPerson: "",
                    caseSource: "",
                    investigatorsId: this.$store.state.user.login.loginId,
                    investigatorsName: this.$store.state.user.login.userName,
                    handlingDepartment: this.$store.state.user.login.orgId,
                    handlingUnit: "",
                    coInvestigatorsId: "",
                    coInvestigatorsName: "",
                    party: "",
                    partyType: "",
                    partyIdcard: "",
                    reasonId: "",
                    reasonVersion: ""
                },
                inputVisible: false,
                userList: [],
                quotaInvoice: {},
                casePartList:[],
                inputValue: "",
                reasonList: [],
                form2: {},
                rules: {}
            };
        },
        methods: {
            querySearchAsync(queryString, cb) {
                let arr = [];
                this.labelList.forEach(i => {
                    arr.push({value: i.labelName});
                });
                cb(arr);
            },
            closeDialog() {
                this.$emit("update:show", false);
            },
            showInput() {
                this.inputVisible = true;
            },
            submitForm: async function () {
                let x = true;
                this.$refs.step1.submitForm(res => {
                    if (!res) {
                        x = res;
                    }
                });
                this.$refs.step2.submitForm(res => {
                    if (!res) {
                        x = res;
                    }
                });
                this.$refs.step3.submitForm(res => {
                    if (!res) {
                        x = res;
                    }
                });
                this.$refs.step4.submitForm(res => {
                    if (!res) {
                        x = res;
                    }
                });
                this.$refs.step5.submitForm(res => {
                    if (!res) {
                        x = res;
                    }
                });
                this.$refs.step6.submitForm(res => {
                    if (!res) {
                        x = res;
                    }
                });
                if (x) {
                    if (this.form.paymentMethod == 1 && this.form.isProcessingPunish == 1 ){
                        if (this.form.total == Number(this.form.penaltyAmount)) {
                            this.saveForm1().then(() => {
                                this.$emit("upDateList");
                                this.closeDialog();
                            });
                        }else{
                            this.$confirm('票据与罚款金额不一致, 是否继续?', '提示', {
                                confirmButtonText: '确定',
                                cancelButtonText: '取消',
                                type: 'warning'
                            }).then(() => {
                                this.saveForm1().then(() => {
                                    this.$emit("upDateList");
                                    this.closeDialog();
                                });
                            })
                        }
                    } else {
                        if (!this.form.penaltyAmount || this.form.paidAmount == this.form.penaltyAmount ) {
                            this.saveForm1().then(() => {
                                this.$emit("upDateList");
                                this.closeDialog();
                            });
                        } else {
                            this.$confirm('实缴金额与处罚金额不一致, 是否继续?', '提示', {
                                confirmButtonText: '确定',
                                cancelButtonText: '取消',
                                type: 'warning'
                            }).then(() => {
                                this.saveForm1().then(() => {
                                    this.$emit("upDateList");
                                    this.closeDialog();
                                });
                            })
                        }
                    }
                }
            },
            getFileList() {
                if (this.type) {
                    apiFileGetDocList(this.type).then(res => {
                        this.fileList = res.docList;
                    });
                } else {
                    this.$message({
                        type: "error",
                        message: "请先保存基本信息!"
                    });
                }
            },
            saveForm1() {
                return new Promise(resolve => {
                    let arr = [];
                    if (this.type) {
                        this.form.scId = this.type;
                        apiSimpleCaseUpdate(this.form).then(() => {
                            resolve();
                            this.$message({
                                type: "success",
                                message: "修改成功!"
                            });
                        });
                    } else {
                        apiSimpleCaseAdd(this.form).then(res => {
                            this.form2.scId = res.scId;
                            this.type = res.scId;
                            this.$emit("update:type", res.scId);
                            resolve();
                            this.$message({
                                type: "success",
                                message: "保存成功!"
                            });
                        });
                    }
                });
            },
            getLabel() {
                apiSimpleCaseGetLabel(this.type).then(res => {
                    this.dynamicTags = res.data;
                });
            },
            beforeUploadFile() {
                return this.type || this.form2.scId;
            },
            handleInputConfirm() {
                if (this.type || this.form2.scId) {
                    let json = {
                        labelName: this.inputValue,
                        caseType: 1,
                        scId: this.type
                    };
                    apiSimpleCaseAddCaseLabel(json).then(() => {
                        this.inputVisible = false;
                        this.inputValue = "";
                        this.getLabel();
                    });
                } else {
                    this.$message({
                        type: "error",
                        message: "请先保存基本信息!"
                    });
                }
            },
            handleClose(data) {
                apiSimpleCaseDeleteCaseLabel(data.labId).then(() => {
                    this.getLabel();
                });
            },
            getDetail() {
                this.loading = true;
                apiSimpleCaseDetailGet(this.type).then(res => {
                    this.quotaInvoice = res.quotaInvoice;
                    this.fileList = res.fileData;
                    this.dynamicTags = res.caseLableData;
                    res.simpleCaseData.casePartyList = res.casePartyList;
                    this.form = res.simpleCaseData;
                    if (this.form.performanceTime == 0) {
                        this.form.performanceTime = ''
                    }
                    if (this.form.coInvestigatorsId) {
                        this.coInvestigatorsId = this.form.coInvestigatorsId.split(",");
                    }
                    this.org = [this.form.handlingUnit, this.form.handlingDepartment];
                    this.stepNum = "jiben";
                    this.loading = false;
                });
            },
            initPage() {
                apiSearchLabel({pageSize: 5, pageNum: 1}).then(res => {
                    this.labelList = res.data;
                });
                this.getDetail();
            }
        },
        created() {
            this.initPage();
        }
    };
</script>
<style lang='less' scoped>
    /deep/ .el-dialog__body {
        overflow: auto;
        height: calc(~"90vh - 150px");
    }
    .checkIdCard {
        position: absolute;
        right: 10px;
        top: 50%;
        z-index: 999;
    }
    .easyCase{
        /deep/.el-dialog{
            min-width: 870px;
        }
    }
    .wrop_col {
        border-right: 1px solid #eeeeee;
        max-height: 70vh;
        padding-bottom: 20px;
        overflow: auto;
    }

    .add-button {
        border: 3px dashed #e1e7dc;
        margin: 5px;
        border-radius: 4px;
        text-align: center;
        color: #888;
        cursor: pointer;
    }

    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }


    .upload-div {
        width: 100px;
        height: 100px;
        margin: auto;
        padding: 10px;
        background: #5695ff;
        color: #eeeeee;
        border: 1px solid #cccccc;
        border-radius: 8px;
        display: flex;
    }

    .el-tag + .el-tag {
        margin-left: 10px;
    }

    .button-new-tag {
        margin-right: 10px;
        height: 32px;
        line-height: 30px;
        padding-top: 0;
        padding-bottom: 0;
    }

    .input-new-tag {
        width: 90px;
        margin-right: 10px;
        vertical-align: bottom;
    }
    .el-tag{
        margin-right: 10px;
    }
    .title {
        font-weight: bold;
        padding: 5px 30px;
        margin-bottom: 20px;
        margin-left: 50px;
        margin-top: 0;
        color: #aaaaaa;
        border-bottom: 1px solid #eeeeee;
    }

    .demo-table-expand {
        font-size: 0;
    }

    /deep/ .el-collapse-item {
        background: rgba(255, 231, 226, 0.2);

        &.hasItem {
            background: rgba(237, 255, 253, 0.2);
        }
    }

    /deep/ .el-collapse-item__header {
        background: initial;
        padding: 0 5px;
    }

    /deep/ .el-collapse-item__wrap {
        background: initial;
        padding: 0 5px;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }

    /deep/ .el-table .warning-row {
        background: #f5cfcf;
    }

    /deep/ .el-table .success-row {
        background: #f0f9eb;
    }

    .text-css {
        text-align: left;
    }
</style>


