<template>
    <el-form :model="form" :rules="rules" label-width="140px" ref="ruleForm" status-icon>
        <el-row :gutter="20">
            <el-col :span="12" style="margin:20px 0 -20px 0">
                <el-form-item label="案件来源：" prop="caseSource">
                    <el-cascader
                            :options="caseSourceList"
                            :props="{value:'dictValue',label:'dictName'}"
                            :show-all-levels="false"
                            @change="handleChange"
                            expand-trigger="hover"
                            style="display: block"
                            v-model="caseSource">
                    </el-cascader>
                </el-form-item>
            </el-col>
            <el-col :span="12" style="margin:20px 0 -20px 0" v-if="caseSource.length > 1">
                <el-form-item :label="caseSource[1] == 'other'? '其他途径':'来源编号'" prop="caseSource">
                    <el-input v-model="form.caseSourceNum"/>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="24">
                <el-divider content-position="left">
                    <div style="color: #ccc;font-size: 12px">当事人信息</div>
                </el-divider>
            </el-col>
        </el-row>
        <div v-for="(data,index) in form.casePartyList" :key="index" class="party-list">
            <el-form :model="data" :rules="rules2" label-width="140px" ref="ruleForm2" status-icon>
            <el-row :gutter="20">
                <el-col :span="24">
                    <el-form-item label="当事人类型：" prop="partyType">
                        <el-radio-group v-model="data.partyType">
                            <el-radio label="1">个人</el-radio>
                            <el-radio label="2">单位</el-radio>
                            <el-radio label="3">个体户</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="12">
                    <el-form-item label="当事人：" prop="party">
                        <el-input v-model="data.party">
                            <template slot="append">
                                <el-button @click="deleteParty(index)">删除该当事人</el-button>
                            </template>
                        </el-input>
                    </el-form-item>
                </el-col>

            </el-row>
            <el-row :gutter="20">
                <el-col :span="12">
                    <el-form-item label="负责人类型：" prop="legalPerson" v-if="data.partyType != 1">
                        <el-radio-group v-model="data.chargeLegalType">
                            <el-radio :label="1">法人</el-radio>
                            <el-radio :label="2">负责人</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item :label="data.chargeLegalType == 2 ? '负责人：' :'法定代表人：'" prop="legalPerson" v-if="data.partyType != 1">
                        <el-input v-model="data.legalPerson"/>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="12">
                    <el-form-item label="当事人证件类型：">
                        <el-row>
                            <el-col :span="16">
                                <el-select class="no-radius-left" v-if="data.partyType == 2" v-model="data.partyCertificateType">
                                    <el-option :key="data.dictValue" :label="data.dictName" :value="data.dictValue"
                                               v-for="data in certificatesType"></el-option>
                                </el-select>
                                <el-select class="no-radius-left" v-if="data.partyType != 2 && data.partyType != 3" v-model="data.partyCertificateType">
                                    <el-option :key="data.dictValue" :label="data.dictName"
                                               :value="data.dictValue" v-for="data in personalCertificatesType"></el-option>
                                </el-select>
                                <el-select class="no-radius-left" v-if="data.partyType == 3" v-model="data.partyCertificateType">
                                    <el-option :key="data.dictValue" :label="data.dictName"
                                               :value="data.dictValue" v-for="data in selfEmployedWorker"></el-option>
                                </el-select>
                            </el-col>
                            <el-col :span="8">
                                <el-button class="no-radius-right" icon="el-icon-tickets">读取证件</el-button>
                            </el-col>
                        </el-row>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="当事人证件号码：">
                        <el-input v-model="data.partyIdcard"/>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="12">
                    <el-form-item label="当事人联系地址：">
                        <el-input v-model="data.partyAddress"/>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="当事人联系电话：" prop="partyTel">
                        <el-input v-model="data.partyTel"/>
                    </el-form-item>
                </el-col>
            </el-row>
            </el-form>
        </div>
        <el-row>
            <el-col :span="24">
                <el-form-item>
                    <el-button @click="addParty">添加当事人</el-button>
                </el-form-item>
            </el-col>
        </el-row>
    </el-form>
</template>

<script>
    import {getTree} from "../../../../../until/menuList";

    export default {
        name: "party",
        props: ['form','casePartList'],
        data() {
            let checkIdNumR = (rule, value, callback) => {
                const reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
                if (!value) {
                    callback();
                } else if (!reg.test(value)) {
                    return callback(new Error('证件号码格式不正确'))
                } else {
                    callback();
                }
            };
            return {
                caseSourceList: [],
                caseSource: [],
                selfEmployedWorker:[],
                personalCertificatesType: [],
                certificatesType: [],
                rules2:{
                    partyIdcard: [{validator: checkIdNumR, trigger: 'change'}],
                    partyType: [{required: true, message: '请选择当事人类型', trigger: 'change'}],
                    party: [{required: true, message: '请输入当事人名称', trigger: 'change'}],
                    partyTel: [{required: true, message: '请输入当事人联系方式', trigger: 'change'}],
                },
                rules: {
                    caseSource: [{required: true, message: '请选择案件来源', trigger: 'change'}],

                }
            }
        },
        methods: {
            addParty(){
                this.form.casePartyList.push({partyType:'',chargeLegalType:'',party:'',legalPerson:'',partyCertificateType:'',partyIdcard:'',partyAddress:'',partyTel:''})
            },
            deleteParty(index){
                if (index == 0 && this.form.casePartyList.length == 1){
                    this.form.casePartyList = [{partyType:'',chargeLegalType:'',party:'',legalPerson:'',partyCertificateType:'',partyIdcard:'',partyAddress:'',partyTel:''}]
                } else {
                    this.form.casePartyList.splice(index,1)
                }
            },
            handleChange(data) {
                this.form.caseSource = data[data.length - 1];
            },
            submitForm(func) {
                this.$refs.ruleForm.validate(valid => {
                    func(valid)
                })
            },
            initPage() {
                this.$until.workBookApi('caseSource').then(res => {
                    this.caseSourceList = getTree(res, 'dictValue', 'subDict', false, true);
                    if (this.form.caseSource) {
                        let thisCase = this.caseSourceList.find(i => {return i.dictValue == this.form.caseSource});
                        if (thisCase) {
                            this.caseSource = [this.form.caseSource]
                        } else {
                            this.caseSource = ['caseSourceJb', this.form.caseSource]
                        }
                    }
                });
                this.$until.workBookApi('personalCertificatesType').then(res => {
                    this.personalCertificatesType = res
                });
                this.$until.workBookApi('selfEmployedWorker').then(res => {
                    this.selfEmployedWorker = res
                });
                this.$until.workBookApi('certificatesType').then(res => {
                    this.certificatesType = res
                });

            }
        },
        created() {
            this.initPage()
        }
    }
</script>

<style lang="less" scoped>
    .no-radius-right {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        display: block;
        width: 100%;
        height: 32px;
    }
    .party-list{
        border: 1px solid #eeeeee;
        margin: 10px 0;
        padding: 10px 5px;
    }
    .no-radius-left {
        display: block;
        /deep/ .el-input__inner {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            border-right: 0;
        }
    }



</style>
