<template>
    <el-form :model="form" :rules="rules" label-width="180px" ref="ruleForm" status-icon>
        <el-row :gutter="20">
            <el-col :span="24">
                <el-divider content-position="left">
                    <div style="color: #ccc;font-size: 12px">违法信息</div>
                </el-divider>
            </el-col>

            <el-col :span="12">
                <el-form-item label="执法领域：" prop="enforceId">
                    <el-select @change="enforceChange" style="display: block" v-model="form.enforceId">
                        <el-option :key="data.enforceId" :label="data.enforceName" :value="data.enforceId"
                                   v-for="data in lefList"/>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="违法类型：">
                    <el-select @change="changeType" style="display: block" v-model="form.illegalTypes">
                        <el-option :key="data.dictValue" :label="data.dictName" :value="data.dictValue"
                                   v-for="data in typeList"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="24">
                <el-form-item label="违法行为：" prop="reasonId">
                    <el-select @change="changeReason" filterable style="display: block" v-model="form.reasonId">
                        <el-option :key="data.reasonId" :label="data.reasonNum + ' ' + data.reasonName"
                                   :value="data.reasonId"
                                   v-for="data in reasonList"/>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="24" v-if="form.accordingRegulationsContent">
                <el-form-item label="">
                    <table style="width: 100%;">
                        <tr>
                            <td align="center" class="title">法律责任适用条款</td>
                        </tr>
                        <tr>
                            <td>{{form.accordingRegulationsContent}}</td>
                        </tr>
                    </table>
                </el-form-item>
            </el-col>
            <el-col :span="24" v-if="form.accordingRegulationsContent">
                <el-form-item label="">
                    <table>
                        <tr>
                            <td align="center" class="title">法律法规规章规定</td>
                        </tr>
                        <tr>
                            <td>{{form.violationRegulationsContent}}</td>
                        </tr>
                    </table>
                </el-form-item>
            </el-col>
        </el-row>
    </el-form>
</template>
<script>
    import {apiGetAmountByCause, apiLefLisGett, apiSimpleCaseSearchReasonByType} from "../../../../../api/case";

    export default {
        name: "illegalMsg",
        data() {
            return {
                typeList: [],
                changeFlag: true,
                caseSourceList: [],
                lefList: [],
                reasonList: [],
                rules: {
                    enforceId: [{required: true, message: '请选择执法领域', trigger: 'change'}],
                    illegalTypes: [{required: true, message: '请选择违反法类型', trigger: 'change'}],
                    reasonId: [{required: true, message: '请选择案由', trigger: 'change'}],
                }
            }
        },
        props: ['form', 'amountList', 'type'],
        methods: {
            changeReason(data) {
                let reason = this.reasonList.find(i => {
                    return i.reasonId === data
                });
                this.form.reason = reason;
                this.form.reasonName = reason.reasonName;
                this.form.illegalTypes = reason.illegalTypes;
                this.form.illegalTypesName = reason.illegalTypesName;
                this.form.accordingRegulationsContent = reason.accordingRegulationsContent;
                this.form.violationRegulationsContent = reason.violationRegulationsContent;
                this.form.reasonVersion = reason.version;
                apiGetAmountByCause({
                    pageNum: 1,
                    pageSize: 3,
                    reasonId: data,
                    reasonVersion: reason.version
                }).then(res => {
                    this.$emit('update:amountList', res.data)
                });
            },
            submitForm(func) {
                this.$refs.ruleForm.validate(valid => {
                    func(valid)
                })
            },
            // 执法领域change
            enforceChange(data) {
                let thisEnforce = this.lefList.find(i => {
                    return i.enforceId === data
                });
                this.form.enforceName = thisEnforce.enforceName;
                let json = {
                    enforceId:this.form.enforceId
                };
                if (this.type) {
                    json.caseId = this.type;
                }
                apiSimpleCaseSearchReasonByType({enforceId: this.form.enforceId}).then(res => {
                    this.reasonList = res.data;
                });
                this.$until.workBookApi(thisEnforce.enforceCode).then(res => {
                    this.typeList = res;
                    if (this.changeFlag) {
                        this.form.illegalTypes = '';
                        this.form.reasonId = '';
                    }
                })
            },
            changeType(data) {
                this.form.illegalTypesName = this.typeList.find(i => {
                    return i.dictValue === data
                }).dictName;
                if (this.changeFlag) {
                    this.form.reasonId = '';
                }
                apiSimpleCaseSearchReasonByType({enforceId: this.form.enforceId, illegalTypes: data}).then(res => {
                    this.reasonList = res.data
                })
            },
            initPage() {
                this.changeFlag = false;
                this.$until.workBookApi('caseSource').then(res => {
                    this.caseSourceList = res
                });
                apiLefLisGett({pageSize: 1000, pageNum: 1}).then(res => {
                    this.lefList = res.data;
                    if (this.form.enforceId) {
                        let code = this.lefList.find(i => {
                            return i.enforceId === this.form.enforceId
                        }).enforceCode;
                        this.$until.workBookApi(code).then(res => {
                            this.typeList = res;
                            if (this.form.illegalTypes) {
                                this.form.illegalTypesName = this.typeList.find(i => {
                                    return i.dictValue === this.form.illegalTypes
                                }).dictName;
                                apiSimpleCaseSearchReasonByType({
                                    enforceId: this.form.enforceId,
                                    illegalTypes: this.form.illegalTypes
                                }).then(res => {
                                    this.reasonList = res.data;
                                    this.changeFlag = true;
                                })
                            } else {
                                this.changeFlag = true;
                            }
                        })
                    }else{
                        this.changeFlag = true;
                    }
                })
            }
        },
        created() {
            this.initPage()
        }

    }
</script>

<style lang="less" scoped>
    table {
        .gridTable;

        .title {
            background: #f0f0f0;
        }
    }

</style>
