<template>
    <el-form :model="form2" :rules="rules" label-width="140px" ref="ruleForm" status-icon>
        <el-row>
            <el-col :span="24">
                <el-divider content-position="left">
                    <div style="color: #ccc;font-size: 12px">文书</div>
                </el-divider>
            </el-col>
            <el-col :key="item.typeName" :span="24" v-for="item in documentList">
                <el-form-item :label="item.docName+ '：'" :required="!!item.isMust">
                    <lj-upload
                            :fileChange="fileChange"
                            :item="item"
                            style="width: auto">
                        <el-button icon="el-icon-upload2" size="small">点击上传</el-button>
                        <span class="el-upload__tip" slot="tip" style="padding-left: 10px">只能上传图片格式文件,大小不超过{{item.uploadMaxSize}}MB</span>
                    </lj-upload>
                    <div :key="data.value" class="text item" v-for="data in filterList(item.docTypeId)">
                        <img :src="seeImgApi+data.thumbnailFileId" @click="showImg(data)" alt=""
                             style="width: 50px;height: 50px">{{data.fileName}}
                        <div style="float: right">
                            <el-tooltip :open-delay="500" content="查看当前文书" placement="top">
                                <el-link @click.stop="showImg(data)" icon="el-icon-search" type="primary"></el-link>
                            </el-tooltip>
                            <el-tooltip :open-delay="500" content="修改" placement="top">
                                <el-link @click.stop="dowImg(data)" icon="el-icon-download" style="margin-left: 5px"
                                         type="primary"></el-link>
                            </el-tooltip>
                            <el-tooltip :open-delay="500" content="删除当前项" placement="top">
                                <el-link @click.stop="delImg(data)" icon="el-icon-delete" style="margin-left: 5px"
                                         type="danger"></el-link>
                            </el-tooltip>
                        </div>
                    </div>

                </el-form-item>
            </el-col>
        </el-row>
        <el-dialog top="calc(50vh - 200px)"
                   class="img-dialog"
                   :visible=" percentage > 0"
                   append-to-body
                   :close-on-click-modal="false"
                   :show-close="false">
            <div class="progress-block">
                <el-progress type="circle" :percentage="percentage"></el-progress>
                <br>
                文件上传中，请稍后...
            </div>

        </el-dialog>
        <el-dialog :visible.sync="imgShow"
                   append-to-body
                   class="img-dialog"
                   width="80%">
            <el-carousel
                    :autoplay="false"
                    :loop="false"
                    height="500px"
                    indicator-position="outside"
                    ref="imgRom">
                <el-carousel-item :key="item.docId" v-for="item in fileList">
                    <div class="img-title">{{item.fileName}}</div>
                    <img :alt="item.fileName" :src="seeImgApi + item.fileId" style="width: 100%;height: 100%">
                </el-carousel-item>
            </el-carousel>
        </el-dialog>
    </el-form>
</template>

<script>
    import {apiDocumentTypeSearch, apiFileDelete, apiFileDownLoad, seeImgApi, uploadApi} from "../../../../../api/case";
    import md5 from 'js-md5'
    import LjUpload from '../../../../../components/LjUpload'
    export default {
        name: "book",
        props: ['form2', 'fileList', 'type'],
        components:{LjUpload},
        data() {
            return {
                seeImgApi: seeImgApi,
                imgShow: false,
                percentage:0,
                documentList: [],
                rules: {
                    pmDecisionCode: [{required: true, message: '请填写处罚决定书编号', trigger: 'blur'}],
                },
                addObj: {
                    show: false,

                }
            }
        },
        computed: {
            filterList() {
                return function (o) {
                    return this.fileList.filter(i => {
                        return i.docTypeId == o
                    })
                }
            }
        },
        methods: {
            dowImg(data) {
                apiFileDownLoad(data.fileId)
            },
            fileChange(files,item){
                this.percentage = 0;
                uploadApi({
                    file:files,
                    caseType: 'simple',
                    caseId: this.type ? this.type : '',
                    docTypeId: item.docTypeId,
                    docName: item.docName
                },this.progress).then(res=>{
                    if (res.caseId) {
                        this.form2.scId = res.caseId;
                        this.$emit('update:type', res.caseId)
                    }
                    this.percentage = 0;
                    this.$emit('getFileList')
                }).catch(()=>{
                    this.percentage = 0;
                });
            },
            progress(data){
                this.percentage = parseInt(data.loaded/data.total * 100);
            },
            showImg(data) {
                this.imgShow = true;
                this.$nextTick(() => {
                    this.$refs.imgRom.setActiveItem(this.fileList.indexOf(data));
                })
            },
            submitForm(func) {
                this.$refs.ruleForm.validate(valid => {
                    func(valid)
                })
            },
            delImg(data) {
                this.$confirm('此操作将永久删除该文书, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiFileDelete(data.docId).then(() => {
                        this.$emit('getFileList')
                    })
                })
            },

        },
        created() {
            apiDocumentTypeSearch({
                caseType: "simple",
                pageNum: 1,
                pageSize: 1000,
                orderName: 'isMust',
                sortName: ''
            }).then(res => {
                this.documentList = res.data;
            });
        }
    }
</script>

<style lang="less" scoped>
    .progress-block{
        color: white;
        text-align: center;
        /deep/.el-progress__text{
            color: white;
        }
    }
    .img-title {
        color: white;
        text-align: center;
        line-height: 30px;
        font-size: 25px;
    }

    .text.item {
        width: 300px;
        float: left;
        line-height: 50px;
        margin: 5px 5px 5px 0;
        border: 1px solid #eeeeee;
        padding: 5px;
        vertical-align: middle;

        img {
            vertical-align: middle;
            margin-right: 5px;
        }

        &.add {
            text-align: center;
            transition: all .5s;
            cursor: pointer;

            &:hover {
                background: #6d8eff;
                color: white;
            }
        }
    }

    .img-dialog {
        background: transparent;

        /deep/ .el-dialog {
            box-shadow: none;
            background: transparent;
        }

        /deep/ .el-dialog__header {
            background: transparent;
        }
    }
</style>
