<template>
    <el-form :model="form" :rules="rules" label-width="180px" ref="ruleForm" status-icon>

        <el-row :gutter="20">
            <el-col :span="24">
                <el-divider content-position="left">
                    <div style="color: #ccc;font-size: 12px">办案信息</div>
                </el-divider>
            </el-col>
            <el-col :span="12">
                <el-form-item label="办案部门：" prop="handlingDepartment">
                    <el-select @change="changeHandlingDepartment" disabled style="display: block"
                               v-model="form.handlingDepartment">
                        <el-option :key="data.orgId" :label="data.orgName" :value="data.orgId"
                                   v-for="data in orgList"/>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="主办人：" prop="investigatorsId">
                    <el-select @change="changeInvestigators" style="display: block" v-model="form.investigatorsId">
                        <el-option
                                :disabled="!form.coInvestigatorsId || form.coInvestigatorsId.indexOf(data.loginId) > -1"
                                :key="data.loginId"
                                :label="data.fullName" :value="data.loginId"
                                v-for="data in userList"/>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="24">
                <el-form-item label="协办人：" prop="coInvestigatorsId">
                    <el-select @change="changeCoInvestigatorsId" multiple style="display: block"
                               v-model="coInvestigatorsId">
                        <el-option :disabled="data.loginId == form.investigatorsId" :key="data.loginId"
                                   :label="data.fullName" :value="data.loginId"
                                   v-for="data in userList"/>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>
    </el-form>
</template>

<script>
    import {apiOrgSearch} from "../../../../../api/system";
    import {apiCommonGetOrgUser} from "../../../../../api/case";

    export default {
        name: "workMsg",
        props: ['form'],
        data() {
            return {
                orgList: [],
                userList: [],
                changeFlag: true,
                coInvestigatorsId: [],
                rules: {
                    investigatorsId: [{required: true, message: '请选择主办人', trigger: 'change'}],
                    coInvestigatorsId: [{required: true, message: '请选择协办人', trigger: 'change'}],
                    handlingDepartment: [{required: true, message: '请选择办案部门', trigger: 'change'}],
                }
            }
        },
        methods: {
            // 主办人change
            changeInvestigators(data) {

                this.form.investigatorsName = this.userList.find(i => {
                    return i.loginId === data
                }).fullName
            },
            submitForm(func) {
                this.$refs.ruleForm.validate(valid => {
                    func(valid)
                })
            },
            changeHandlingDepartment(data) {
                apiCommonGetOrgUser(data).then(res => {
                    this.userList = res.userList;
                    if (this.changeFlag) {
                        this.form.investigatorsId = '';
                        this.form.coInvestigatorsName = '';
                        this.form.coInvestigatorsId = '';
                        this.coInvestigatorsId = []
                    }
                });

            },
            changeCoInvestigatorsId(data) {
                let arr = [];
                data.forEach(i => {
                    arr.push(this.userList.find(x => {
                        return x.loginId === i
                    }).fullName)
                });
                this.form.coInvestigatorsId = data.join(',');
                this.form.coInvestigatorsName = arr.join(',');
            },
            initPage() {
                this.changeFlag = false;
                apiCommonGetOrgUser(this.form.handlingDepartment).then(res => {
                    this.userList = res.userList;
                    apiOrgSearch().then(res => {
                        if (this.form.coInvestigatorsId && typeof this.form.coInvestigatorsId === 'string') {
                            this.coInvestigatorsId = this.form.coInvestigatorsId.split(',');
                        }
                        this.orgList = res.data;
                        this.changeFlag = true;
                    })
                })
            }
        },
        created() {
            this.initPage()
        }
    }
</script>

<style scoped>

</style>
