<template>
    <el-form :model="form" :rules="rules" label-width="180px" ref="ruleForm" status-icon>
        <el-row :gutter="20">

            <el-col :span="24">
                <el-divider content-position="left">
                    <div style="color: #ccc;font-size: 12px">案件信息</div>
                </el-divider>
            </el-col>

            <el-col :span="24" class="col-local">
                <el-form-item label="案发地点：" prop="crimeLocal">
                    <el-row>
                        <el-col :span="6">
                            <el-select placeholder="请选择" v-model="form.district" class="wrop_rel">
                                <el-option :key="data.areaId" :label="data.areaName" :value="data.areaId"
                                           v-for="data in areaList"></el-option>
                            </el-select>
                        </el-col>

                        <el-col :span="12">
                            <el-input @change="searchAddress" v-model="form.crimeLocal" />
                        </el-col>
                        <el-col :span="6">
                            <el-cascader class="wrop_sel"
                                         :options="crimeLocalType"
                                         :props="{value:'dictValue',label:'dictName',multiple: 'true'}"
                                         :show-all-levels="false"
                                         @change="handleChange"
                                         expand-trigger="hover"
                                         v-model="localType">
                            </el-cascader>
                        </el-col>

                    </el-row>
                </el-form-item>
            </el-col>
            <el-col :span="24">
                <el-form-item label="" prop="crimeLocal">
                    <div style="width: 100%;height: 300px;">
                        <el-amap
                                :amapManager="amapManager"
                                :center="mapObj.center"
                                :events="mapObj.events"
                                :plugin="mapObj.plugins"
                                :scrollWheel="false"
                                :zoom="mapObj.zoom"
                                class="amap-demo"
                                vid="amap"
                        >
                        </el-amap>
                    </div>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="违法行为发现时间：" prop="crimeTime">
                    <el-date-picker
                            placeholder="选择日期时间"
                            style="display: block;width: 100%"
                            type="datetime"
                            v-model="form.crimeTime"
                            value-format="timestamp"
                            format="yyyy/MM/dd HH:mm">
                    </el-date-picker>
                </el-form-item>
            </el-col>
        </el-row>
    </el-form>
</template>

<script>
    import {AMapManager} from 'vue-amap';
    import {apiGetOrgAtea} from "../../../../../api/case";
    import mapImg from '../../../../../assets/map.png'
    import {getTree} from "../../../../../until/menuList";

    export default {
        name: "caseTimeAddress",
        props: ['form'],
        data() {
            let vm = this;
            return {
                amapManager: new AMapManager(),
                areaList: [],
                localType:[],
                crimeLocalType:[],
                mapObj: {
                    events: {
                        init(o) {
                            vm.map = o;
                            AMapUI.loadUI(['misc/PositionPicker'], function (PositionPicker) {
                                vm.positionPicker = new PositionPicker({
                                    mode: 'dragMarker',
                                    map: o,
                                    iconStyle:{//自定义外观
                                        url:mapImg,//图片地址
                                        size:[30,40],  //要显示的点大小，将缩放图片
                                        ancher:[15,30],//锚点的位置，即被size缩放之后，图片的什么位置作为选中的位置
                                    }
                                });
                                vm.positionPicker.on('success', function (positionResult) {
                                    vm.mapObj.result = positionResult.regeocode.pois;
                                    vm.form.latitude = positionResult.position.lat;
                                    vm.form.longitude = positionResult.position.lng;
                                    vm.mapObj.address = positionResult.regeocode.formattedAddress;
                                    vm.mapObj.center = [positionResult.position.lng, positionResult.position.lat]
                                });

                            })
                        }
                    },
                    address: '',
                    result: '',
                    zoom: 20,
                    center: this.form.latitude && this.form.longitude ? [this.form.longitude, this.form.latitude] : [121.329402, 31.228667],
                    plugins: ['ToolBar'],
                },
                rules: {
                    crimeTime: [{required: true, message: '请选择案发时间', trigger: 'change'}],
                    crimeLocal: [{required: true, message: '请填写案发地点', trigger: 'blur'}]
                }
            }
        },
        created() {
            this.$until.workBookApi('crimeLocalType').then(res => {
                res.forEach(i=>{
                    i.children=[]
                });
                this.crimeLocalType = res;
                this.crimeLocalType.forEach(i=>{
                    this.$until.workBookApi(i.dictValue).then(res2=>{
                        i.children = res2
                    })
                });
                if (this.form.crimeLocalType) {
                    this.crimeLocalType.forEach(i=>{
                        i.children.forEach(j=>{
                            if(j.dictValue == this.form.crimeLocalType){
                                this.localType = [i.dictValue, j.dictValue]
                            }
                        })
                    });
                }
            });
            apiGetOrgAtea(this.form.handlingDepartment).then(res => {
                this.areaList = res.areaList;
                if (this.form.crimeLocal) {
                    this.positionPicker.start([this.form.longitude, this.form.latitude]);
                    this.mapObj.center = [this.form.longitude, this.form.latitude]
                } else {
                    this.form.district = res.areaList[0].areaId;
                    let vm = this;
                    AMap.plugin('AMap.Geocoder', () => {
                        let geocoder = new AMap.Geocoder({
                            city: vm.$store.state.user.areaId
                        });
                        geocoder.getLocation(res.areaList[0].areaName, function (status, result) {
                            if (status === 'complete' && result.info === 'OK') {
                                // result中对应详细地理坐标信息
                                vm.form.latitude = result.geocodes[0].location.lat;
                                vm.form.longitude = result.geocodes[0].location.lng;
                                vm.mapObj.center = [result.geocodes[0].location.lng, result.geocodes[0].location.lat]
                            }
                        })
                    })
                }
            })
        },
        methods: {
            handleChange(){
                this.form.crimeLocalType = this.localType[1]
            },
            submitForm(func) {
                this.$refs.ruleForm.validate(valid => {
                    func(valid)
                })
            },
            searchAddress() {
                let vm = this;
                AMap.plugin('AMap.Geocoder', () => {
                    let geocoder = new AMap.Geocoder({
                        city: vm.$store.state.user.areaId
                    });
                    geocoder.getLocation(this.form.crimeLocal, function (status, result) {
                        if (status === 'complete' && result.info === 'OK') {
                            // result中对应详细地理坐标信息
                            vm.form.latitude = result.geocodes[0].location.lat;
                            vm.form.longitude = result.geocodes[0].location.lng;
                            vm.positionPicker.start([result.geocodes[0].location.lng, result.geocodes[0].location.lat]);
                            vm.mapObj.center = [result.geocodes[0].location.lng, result.geocodes[0].location.lat]
                        }
                    })
                })
            },
        }
    }
</script>

<style lang="less" scoped>

    .col-local{
        /deep/ .el-input__inner{
            border-radius: 0;
        }
        .wrop_rel{
            width: 100%;
            /deep/  .el-input--suffix{
                overflow: hidden;
                padding: 0;
                margin: 0;
                /deep/  .el-input__inner{
                    border-top-left-radius: 4px;
                    border-bottom-left-radius: 4px;
                    border-right: none;
                }
            }
        }
        .wrop_sel{
            width: 100%;
            /deep/  .el-input--suffix{
                overflow: hidden;
                padding: 0;
                margin: 0;
                /deep/  .el-input__inner{
                    border-top-right-radius: 4px;
                    border-bottom-right-radius: 4px;
                    border-left: none;
                }
            };
            /deep/  .el-cascader__tags{
                top: 40%;
            }
        }
    }


</style>
