<template>
    <div class="assistant-page">
        <el-popover
                placement="top-start"
                trigger="manual"
                v-if="message"
                v-model="visible"
                width="80">
            <div class="tips-content">
                <div>
                    发现该当事人有过{{message.caseInfoList ? message.caseInfoList.length : 0}}条相关历史
                    <br>
                    <el-link @click="show=true;visible=false" type="primary">点击查看
                    </el-link>
                </div>
            </div>
            <div @click="visible = !visible" class="icon" slot="reference">
                <img src="../../../../assets/icon.png"/>
            </div>
        </el-popover>
        <transition name="el-fade-in-linear">
            <div @click.self="show=false" class="show-detail" v-if="show">
                <div class="detail-content">
                    <div class="header">
                        <img src="../../../../assets/icon.png"> 智能小助手
                        <i class="el-icon-close" @click="show=false"></i>
                    </div>
                    <div class="content">
                        <el-row :gutter="10">
                            <el-col :span="12" class="item-text">
                                当事人类别：
                                    <el-tooltip class="item" effect="dark" :content="searchForm.caseParty.partyType" placement="top-start">
                                        <span>
                                            {{searchForm.caseParty.partyType | partyType}}
                                        </span>
                                    </el-tooltip>
                            </el-col>
                            <el-col :span="12" class="item-text">
                                当事人：
                                <el-tooltip class="item" effect="dark" :content="searchForm.caseParty.party" placement="top-start">
                                        <span>
                                            {{searchForm.caseParty.party}}
                                        </span>
                                </el-tooltip>
                            </el-col>
                            <el-col :span="12" class="item-text">
                                当事人证件类型：
                                <span v-if="searchForm.caseParty.partyType == 1">{{idCardNum(searchForm.caseParty.partyCertificateType) }}</span>
                                <span v-if="searchForm.caseParty.partyType == 2">{{comNum(searchForm.caseParty.partyCertificateType)}}</span>
                            </el-col>
                            <el-col :span="12" class="item-text">
                                当事人证件号码：
                                <el-tooltip class="item" effect="dark" :content="searchForm.caseParty.partyIdcard" placement="top-start">
                                        <span>
                                            {{searchForm.caseParty.partyIdcard}}
                                        </span>
                                </el-tooltip>
                            </el-col>
                            <el-col :span="12" class="item-text">
                                当事人联系电话：
                                <el-tooltip class="item" effect="dark" :content="searchForm.caseParty.partyTel" placement="top-start">
                                        <span>
                                            {{searchForm.caseParty.partyTel}}
                                        </span>
                                </el-tooltip>
                            </el-col>
                            <el-col :span="12" class="item-text">
                                执法领域：
                                <el-tooltip class="item" effect="dark" :content="prop.enforceName" placement="top-start">
                                        <span>
                                            {{prop.enforceName}}
                                        </span>
                                </el-tooltip>
                            </el-col>
                            <el-col :span="12" class="item-text">
                                执法行为：
                                <el-tooltip class="item" effect="dark" :content="prop.reasonName" placement="top-start">
                                        <span>
                                            {{prop.reasonName}}
                                        </span>
                                </el-tooltip>
                            </el-col>
                        </el-row>
                        <el-form label-position="top">
                            <el-row :gutter="20">
                                <el-col :span="12">
                                    <el-form-item label="查询范围">
                                        <el-select v-model="searchForm.district" clearable
                                                   @change="searchList">
                                            <el-option
                                                    v-for="data in cityList"
                                                    :key="data.areaId"
                                                    :label="data.areaName"
                                                    :value="data.areaId"
                                            ></el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="违法行为发现时间">
                                        <el-date-picker
                                                @change="searchList"
                                                clearable
                                                v-model="timeRange"
                                                type="daterange"
                                                value-format="timestamp"
                                                range-separator="至"
                                                start-placeholder="开始日期"
                                                end-placeholder="结束日期">
                                        </el-date-picker>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                        </el-form>
                        <div class="assistant-collapse">
                            此违法行为全市罚款区间：{{message.punishPayMin}}元 ~ {{message.punishPayMax}}元
                        </div>
                        <div class="tips">
                            <img src="../../../../assets/icon.png" alt="">
                            <div class="content">
                                该当事人在此违法行为中被罚款次数：<span class="blue">{{message.payCount}}次</span>，被警告次数：<span class="blue">{{message.warnCount}}次</span>
                                <br>
                                裁量基准罚款范围：<span class="blue">{{message.naturalRangeMin}}元 ~ {{message.naturalRangeMax}}元</span>
                            </div>
                        </div>
                        <el-table
                                :data="message.caseInfoList"
                                :header-cell-style="{background:'#1989fa',color:'white'}"
                                style="width: 100%;border: 1px solid #eeeeee;border-bottom: none">
                            <el-table-column
                                    prop="party"
                                    label="当事人">
                            </el-table-column>
                            <el-table-column
                                    prop="decisionContent"
                                    label="处罚决定">
                            </el-table-column>
                            <el-table-column
                                    prop="crimeLocal"
                                    label="案发地点">
                            </el-table-column>
                            <el-table-column
                                    prop="crimeTime"
                                    label="案发时间">
                            </el-table-column>
                            <el-table-column
                                    width="50"
                                    label="">
                                <template slot-scope="scope">
                                    <el-tooltip :open-delay="500" content="删除当前项" placement="top">
                                        <el-tooltip :open-delay="500" content="查看" placement="top">
                                            <el-link @click.stop="seeFunction(scope.row)" icon="el-icon-search" type="success"></el-link>
                                        </el-tooltip>
                                    </el-tooltip>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="bottom-foot">
                            <el-button @click="clearFrom">清空内容</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
        <see-block :id="seeObj.id" :show.sync="seeObj.show"/>
    </div>
</template>

<script>
    import {apiSearchCaseByIdCard,apiGetArea} from "../../../../api/case";
    import {PARTYTYPE} from "../../../../until/wordBook";
    import SeeBlock from '../component/formItem/see'
    export default {
        name: "intelligentAssistant",
        props: ['prop'],
        components: {SeeBlock},
        data() {
            return {
                timeRange:[],
                visible: false,
                show: false,
                cityList:[],
                personalCertificatesType:[],
                certificatesType:[],
                searchForm:{
                    reasonId:'',
                    reasonVersion:'',
                    district:'',
                    casePartyList:[],
                    startTime:'',
                    endTime:''
                },
                seeObj: {
                    show: false,
                    id: ''
                },
                message: false
            }
        },
        methods: {
            seeFunction(data) {
                this.seeObj.id = data.scId;
                this.seeObj.show = true
            },
            clearFrom(){
                this.timeRange = [];
                this.searchForm.district = '';
                this.searchList()
            },
            searchList: async function(){
                let json = {
                    district:this.searchForm.district,
                    startTime:this.timeRange[0]?this.timeRange[0] : '',
                    endTime:this.timeRange[1]?this.timeRange[1] : '',
                    reasonId: this.searchForm.reasonId,
                    reasonVersion: this.searchForm.reasonVersion,
                    party: this.searchForm.caseParty.party,
                    partyType: this.searchForm.caseParty.partyType,
                    partyIdcard: this.searchForm.caseParty.partyIdcard,
                };
                await apiSearchCaseByIdCard(json).then(res => {
                    this.message = res;
                })
            },
            idCardNum(val) {
                let thisType = this.personalCertificatesType.find(i => {
                    return i.dictValue == val
                });
                if (thisType){
                    return thisType.dictName;
                } else {
                    return ''
                }
            },
            comNum(val) {
                let thisType = this.certificatesType.find(i => {
                    return i.dictValue == val
                });
                if (thisType) {
                    return thisType.dictName;
                } else {
                    return ''
                }
            },
            initPage() {
                this.searchForm.reasonId = this.prop.reasonId;
                this.searchForm.reasonVersion = this.prop.reasonVersion;
                this.searchForm.caseParty = this.prop.casePartyList[0];
                if (
                    this.searchForm.reasonId
                    && this.searchForm.reasonVersion
                    && this.searchForm.caseParty
                    &&  this.searchForm.caseParty.party
                    &&  this.searchForm.caseParty.partyType
                    && this.searchForm.caseParty.partyIdcard ){
                   this.searchList().then(()=>{
                       this.$nextTick(()=>{
                           this.visible = true
                       })
                   })
                }else{
                    this.message = false;
                }

            }
        },
        created(){
            this.$until.workBookApi('personalCertificatesType').then(res => {
                this.personalCertificatesType = res
            });
            this.$until.workBookApi('certificatesType').then(res => {
                this.certificatesType = res
            });
            this.visible = false;
            apiGetArea(this.$store.state.user.areaId).then(res=>{
                apiGetArea(res.areaList[0].areaId).then(res=>{
                    this.cityList = res.areaList
                })
            })

        },
        filters:{
            partyType(val){
                return PARTYTYPE[val]
            }
        },
        watch:{
            prop:{
                deep:true,
                handler(value){
                    console.log(value)
                    this.initPage()
                }
            }
        }

    }
</script>

<style lang="less" scoped>
    .assistant-page{
        /deep/.el-form--label-top .el-form-item__labe{
            padding: 0;
        }
        /deep/.el-date-editor--daterange.el-input__inner{
            width: 100%;
        }
    }
    .bottom-foot{
        position: absolute;
        bottom: 0;
        width: 100%;
        padding: 10px;
        text-align: right;
        left: 0;
        border-top: 1px solid #cccccc;
    }
    .tips{
        margin: 40px 0;
        img{
            width: 40px;
            float: left;
        }
        .content{
            margin-left: 50px;
            background: #e5f7ff;
            border-radius: 4px;
            border: 2px solid #87d7f3;
            line-height: 1.6em;
            .blue{
                color: #4c81df;
            }
        }
    }
    .assistant-collapse{
        background: #d4eaff;
        padding-left: 10px;
        height: 40px;
        margin: 20px 0;
        line-height: 40px;
    }
    .show-detail {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.3);
        z-index: 99998;

        .detail-content {
            position: fixed;
            z-index: 99999;
            top: 0;
            right: 0;
            bottom: 0;
            overflow: auto;
            width: 700px;
            background: white;
            box-shadow: -5px -5px 10px #888;
            .content{
                padding: 10px;
            }
            .item-text{
                overflow: hidden;
                text-overflow:ellipsis;
                white-space: nowrap;
                margin-bottom: 15px;
            }
            .header{
                font-size: @fontSize + 2px;
                font-weight: bold;
                height: 40px;
                line-height: 40px;
                vertical-align: middle;
                border-bottom: 1px solid #cccccc;
                padding-left: 10px;
                img{
                    width: 30px;
                    vertical-align: middle;
                    position: relative;
                    top: -5px;
                }
                .el-icon-close{
                    font-size: @fontSize + 4px;
                    line-height: 40px;
                    float: right;
                    cursor: pointer;
                    margin-right: 10px;
                }
            }
        }
    }

    .icon {
        width: 50px;
        text-align: center;
        height: 60px;
        padding: 10px 5px;
        border-radius: 8px;
        border: 1px solid #cccccc;
        background: white;
        cursor: pointer;
        font-size:  @fontSize ;
        line-height: 60px;
        img{
            width: 100%;
        }
    }

    .status {
        position: absolute;
        top: 20px;
        right: 0px;
        color: red;
        border: 3px solid red;
        font-size: 20px;
        transform: rotate(45deg);
        opacity: 0.2;
    }

    .tips-content {
        font-size:  @fontSize +2;
    }
</style>
