<template>
    <div>
        <el-form :model="form2" :rules="rules" label-width="140px" ref="ruleForm" status-icon>
            <el-row :gutter="20">
                <el-col :span="24">
                    <el-divider content-position="left">
                        <div style="color: #ccc;font-size: 12px">处罚信息</div>
                    </el-divider>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="处罚决定书编号：" prop="pmDecisionCode">
                        <el-input placeholder="请输入处罚决定书编号" v-model="form2.pmDecisionCode"/>
                    </el-form-item>
                </el-col>


            </el-row>
            <el-row :gutter="20">
                <el-col :span="12">
                    <el-form-item label="责令改正：" prop="correctionMethod">
                        <el-checkbox :true-label="1" v-model="form2.correctionMethod">立即</el-checkbox>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="12">
                    <el-form-item :required="true" label="行政处罚：">
                        <el-checkbox :false-label="0" :true-label="1" label="警告"
                                     v-model="form2.isProcessingWarning"></el-checkbox>
                        <el-checkbox :false-label="0" :true-label="1" label="罚款"
                                     v-model="form2.isProcessingPunish"></el-checkbox>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="20" v-if="form2.isProcessingPunish">
                <el-col :span="24">
                    <el-divider content-position="left">
                        <div style="color: #ccc;font-size: 12px">罚款信息</div>
                    </el-divider>
                </el-col>
            </el-row>
            <el-row :gutter="20" prop="isProcessingPunish" v-if="form2.isProcessingPunish">
                <el-col :span="12">
                    <el-form-item :required="true" label="罚款金额：" prop="penaltyAmount">
                        <el-input v-model.number="form2.penaltyAmount">
                            <div slot="append">元</div>
                        </el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <div v-if="form2.casePartyList[0] && form2.casePartyList[0].partyType != 1">
                        <el-tag :key="data.penaltyAmount" @click="form2.penaltyAmount = data.penaltyAmount"
                                v-for="data in amountList">
                            {{data.penaltyAmount}}元
                        </el-tag>
                    </div>
                    <div v-if="form2.casePartyList[0] && form2.casePartyList[0].partyType == 1">
                        <el-tag @click="form2.penaltyAmount = 10">10 元
                        </el-tag>
                        <el-tag @click="form2.penaltyAmount = 20">20 元
                        </el-tag>
                        <el-tag @click="form2.penaltyAmount = 50">50 元
                        </el-tag>
                    </div>
                </el-col>
            </el-row>
            <el-row :gutter="20" v-if="form2.isProcessingPunish">
                <el-col :span="24" v-if="form2.isProcessingPunish">
                    <el-form-item :required="true" label="缴款方式：" prop="paymentMethod">
                        <el-radio-group v-model="form2.paymentMethod">
                            <el-radio :label="1">当场</el-radio>
                            <el-radio :label="2">银行</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
                <el-col :span="12" v-if="form2.paymentMethod == 2">
                    <el-form-item label="缴款银行：">
                        <el-radio label="1" v-model="form2.payingBank">建设银行</el-radio>
                        <el-radio label="2" v-model="form2.payingBank">工商银行</el-radio>
                    </el-form-item>
                </el-col>
                <el-col :span="12" v-if="form2.paymentMethod == 2">
                    <el-form-item label="银行缴款单据编号：">
                        <el-input v-model="form2.paymentReceiptNumber"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12" v-if="form2.paymentMethod == 2">
                    <el-form-item label="实际缴款金额：">
                        <el-input v-model="form2.paidAmount"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="24" v-if="form2.paymentMethod == 1">
                    <el-form-item label="定额票据编号：">
                        <el-row :gutter="0" :key="index" style="margin-bottom: 10px"
                                v-for="(data,index) in selectedObj">
                            <el-col :span="1">
                                <div class="delete-quota" @click="deleteQuota(index)">
                                    <i class="el-icon-close"></i>
                                </div>
                            </el-col>
                            <el-col :span="5">
                                <el-select :disabled="data.quotaInvoiceSelected.length > 0" @change="quotaInvoiceChange(data)"
                                           class="no-radius-left" placeholder="请选择面额"
                                           style="display: block" v-model="data.type">
                                    <el-option :key="data.dictValue" :label="data.dictName" :value="data.dictValue"
                                               v-for="data in quotaInvoice"></el-option>
                                </el-select>
                            </el-col>
                            <el-col :span="18">
                                <el-select
                                        :remote-method="remoteMethod"
                                        @focus="focusSelect(index)"
                                        class="no-radius-right"
                                        clearable
                                        filterable
                                        multiple
                                        remote
                                        reserve-keyword
                                        style="display: block"
                                        v-model="data.quotaInvoiceSelected">
                                    <el-option
                                            :key="item.detailId"
                                            :label="item.quotaInvoiceNum"
                                            :value="item.detailId"
                                            v-for="item in data.quotaInvoiceList">
                                    </el-option>
                                </el-select>
                            </el-col>
                        </el-row>
                    </el-form-item>
                </el-col>
                <el-col :span="24" style="margin-top: -10px"
                        v-if="form2.paymentMethod == 1 && selectedObj.length < quotaInvoice.length">
                    <el-form-item>
                        <el-button @click="addQuota">
                            添加票据
                        </el-button>
                    </el-form-item>
                </el-col>
                <el-col :span="12" v-if="form2.paymentMethod == 2">
                    <el-form-item label="缴款时间：">
                        <el-date-picker
                                placeholder="选择日期时间"
                                style="display: block;width: 100%"
                                type="date"
                                v-model="form2.performanceTime"
                                value-format="timestamp">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    import {apiSearchQuotaByAmount} from "../../../../../api/case";

    export default {
        name: "step2",
        props: ['form2', 'type', 'amountList', 'quotaInvoiceDe'],
        data() {
            let checkPenaltyAmount = (rule, value, callback) => {
                if (this.form2.isProcessingPunish) {
                    if (!value) {
                        return callback(new Error('请填写罚款金额'))
                    } else {
                        return callback()
                    }
                }
            };
            let checkPaymentMethod = (rule, value, callback) => {
                if (this.form2.isProcessingPunish) {
                    if (!value) {
                        return callback(new Error('请填写罚款金额'))
                    } else {
                        callback()
                    }
                }
            };
            return {
                rules: {
                    pmDecisionCode: [{required: true, message: '请填写处罚决定书编号', trigger: 'blur'}],
                    correctionMethod: [{required: true, message: '请选择责令改正方式', trigger: 'blur'}],
                    penaltyAmount: [{validator: checkPenaltyAmount, message: '请填写处罚金额', trigger: 'blur'}],
                    paymentMethod: [{validator: checkPaymentMethod, message: '请填写处罚金额',trigger: 'change'}]
                },
                quotaInvoiceIndex: '',
                quotaInvoice: [],
                selectedObj: [{quotaInvoiceList: [], quotaInvoiceSelected: [], type: '', changeSelectList: []}],
                quotaInvoiceList: [],
                quotaInvoiceSelected: []
            }
        },
        methods: {
            deleteQuota(index){
                if(index == 0 && this.selectedObj.length == 1){
                    this.selectedObj = [{quotaInvoiceList: [], quotaInvoiceSelected: [], type: '', changeSelectList: []}]
                }else {
                    this.selectedObj.splice(index,1)
                }
            },
            quotaInvoiceChange(data) {
                if (this.selectedObj.filter(i => {
                    return i.type == data.type
                }).length > 1) {
                    data.type = '';
                    this.$message({
                        type: 'error',
                        message: '该面额已存在!'
                    });
                }else{
                    apiSearchQuotaByAmount({
                        quotaInvoiceAmount: data.type,
                        pageNum: 1,
                        pageSize: 50,
                        keyWord: ''
                    }).then(res => {
                        data.quotaInvoiceList = res.data
                    })
                }
            },
            submitForm(func) {
                this.$refs.ruleForm.validate(valid => {
                    if (this.form2.paymentMethod == 1 && this.form2.isProcessingPunish == 1 ){
                        this.form2.quotaInvoice = [];
                        this.form2.total = 0;
                        this.selectedObj.forEach(i => {
                            i.changeSelectList=[];
                            i.quotaInvoiceSelected.forEach(x => {
                                let a = i.quotaInvoiceList.find(j => {
                                    return j.detailId == x
                                });
                                if (a && i.changeSelectList.indexOf(a) < 0) {
                                    i.changeSelectList.push(a)
                                }
                            });
                            if (i.changeSelectList.length > 0) {
                                this.form2.total += i.type * i.changeSelectList.length;
                                this.form2.quotaInvoice.push({quotaInvoiceMum: i.type, selectedList: i.changeSelectList})
                            }
                        });

                    }
                    func(valid)

                })
            },
            focusSelect(data) {
                this.quotaInvoiceIndex = data
            },
            addQuota() {
                this.selectedObj.push({quotaInvoiceList: [], quotaInvoiceSelected: [], type: '', changeSelectList: []})
            },
            remoteMethod(data) {
                apiSearchQuotaByAmount({
                    quotaInvoiceAmount: this.selectedObj[this.quotaInvoiceIndex].type,
                    pageNum: 1,
                    pageSize: 50,
                    keyWord: data
                }).then(res => {
                    let array = this.selectedObj[this.quotaInvoiceIndex].changeSelectList.concat(res.data);
                    let n = [];
                    for (let i = 0; i < array.length; i++) {
                        let x = n.find(s => {
                            return s.detailId == array[i].detailId
                        });
                        if (!x) n.push(array[i]);
                    }
                    this.selectedObj[this.quotaInvoiceIndex].quotaInvoiceList = n
                })
            }
        },
        created() {
            this.selectedObj = [{quotaInvoiceList: [], quotaInvoiceSelected: [], type: '', changeSelectList: []}];
            this.$until.workBookApi('quotaInvoice').then(res => {
                this.quotaInvoice = res
            });
            if (this.quotaInvoiceDe && JSON.stringify(this.quotaInvoiceDe) !== '{}') {
                this.selectedObj = [];
                for (let k in this.quotaInvoiceDe) {
                    let arr = [];

                    this.quotaInvoiceDe[k].forEach(i => {
                        arr.push(i.detailId)
                    });
                    apiSearchQuotaByAmount({
                        quotaInvoiceAmount: k,
                        pageNum: 1,
                        pageSize: 50,
                        keyWord: ''
                    }).then(res => {
                        let array = this.quotaInvoiceDe[k].concat(res.data);
                        let n = [];
                        for (let i = 0; i < array.length; i++) {
                            let x = n.find(s => {
                                return s.detailId == array[i].detailId
                            });
                            if (!x) n.push(array[i]);
                        }
                        this.selectedObj.push({
                            quotaInvoiceList: n,
                            quotaInvoiceSelected: arr,
                            type: k,
                            changeSelectList: this.quotaInvoiceDe[k]
                        })
                    })

                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .el-tag{
        cursor: pointer;
        height: 32px;
        line-height: 30px;
        margin-left: 10px
    }
    .delete-quota{
        text-align: center;
        border: 1px solid #DCDFE6;
        height: 32px;
        line-height: 32px;
        border-top-left-radius: 5px;
        cursor: pointer;
        background: #F5F7FA;
        border-bottom-left-radius: 5px;
        &:hover{
            background: #fff;
        }
        .el-icon-close{
            color: red;
        }
    }
    .no-radius-left {
        /deep/ .el-input__inner {
            border-radius: 0;
            border-right: 0;
            border-left: 0;
        }
    }

    .no-radius-right {
        /deep/ .el-input__inner {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
    }

    .text.item {
        width: 250px;
        float: left;
        border: 1px solid #eeeeee;
        line-height: 32px;
        padding: 0 5px;
        margin: 0 3px;

        &.add {
            text-align: center;
            transition: all .5s;
            cursor: pointer;

            &:hover {
                background: #6d8eff;
                color: white;
            }
        }
    }

</style>
